<script>
import numeral from 'numeral';
import viewRequest from '../Cart/view.vue';

  export default {
     components: {viewRequest},
    mounted: function () {
      this.getRequests()
    },
    methods: {
       confirmShow(item){
           this.dialogConfirm = true;
           this.item = item;
       } ,
       confirm(){
          this.dialogW=true
              this.axios
              .post("booking/confirm-client-services", { data: { book: this.item.key} })
              .then(() => { this.dialogConfirm = false, this.getRequests()
              })
       },
       view(viewKey){
           this.dialogView = true;
           this.viewKey = viewKey.key;
       },
      numberFormat(value){
          return numeral(value).format('0,0')
      },

      getNames(item){
          this.dialogW=true
          this.item = item;
              this.axios
              .post("cart/get/request", { data: { book: item.key,type:null} })
              .then(response => { this.items = response.data.data,this.allData = response.data, this.dialogW=false
              });
      },
      getRequests (){
            this.axios
              .get('booking/GetMyRequests')
              .then(
                  response => (
                      this.requestList = response.data,
                      this.isLoadingSearch = !this.isLoadingSearch
                  )
              );
      },
      clickItem(item){
        this.dialog = true
        this.getNames(item)
      }

    },
    data () {
      return {
        item:'',
        isLoadingSearch:true,
        dialogConfirm:false,
        viewKey:'',
        dialogView:false,
        allData:[],
        items:[],
        dialogW:false,
        userID:'',
        dialog: false,
        requestList:[],
        search: '',
        headers: [
            { text: 'OrdeID', value: 'order_id' },
            { text: 'Status', value: 'status' },
            { text: 'Actions', value: 'actions', sortable: false },
          {
            text: 'Last Update',
            align: 'start',
            //sortable: false,
            value: 'updated_at',
          },
          { text: 'Date', value: 'cook_start_date' },
         // { text: 'Time', value: 'Cook_start_time' },
          { text: 'Amount', value: 'boking_amount' },
          //{ text: 'Type of stove', value: 'type_stove_power_source' },
          //{ text: 'Currency', value: 'alphabetic_code' },
          { text: 'Country', value: 'entity' },
          { text: 'State', value: 'state_name' },
          { text: 'Kitchen', value: 'aliase' },
          //{ text: 'Address', value: 'full_address' },
          //{ text: 'Category', value: 'title' },
        ],
      }
    },
  }
</script>
